.docsContainer {

    .container {
        max-width: 1200px;
    }

    ;

    .check li:before {
        content: "\f00c";
        font-family: "FontAwesome";
        font-size: 16px;
        left: 0;
        color: #242424;
        padding-right: 5px;
        position: relative;
        top: 2px;
    }

    .check li {
        font-size: 14px;
        list-style: none;
        margin-bottom: 4px;
    }

    .check {
        margin-left: 0;
        padding-left: 0
    }

    .section {
        padding: 100px 0 0 0;
        position: relative;
        display: block;
    }

    .white {
        background-color: #ffffff;
    }

    strong {
        color: #030303;
    }

    .grey {
        background-color: #f3f3f3;
    }

    .big-title h1 {
        font-size: 48px;
        font-weight: 300;
        margin-bottom: 0;
        text-transform: capitalize;
        padding-bottom: 0;
        text-align: left !important;
    }

    .dark-text {
        font-weight: 300;
    }

    h4 a {
        font-size: 14px;
        font-weight: bold;
    }

    .dark-text a {
        font-size: 14px;
        font-weight: bold;
        padding-left: 20px;
    }

    .dark-text hr {
        width: 130px;
        margin-left: 0;
        margin-top: 40px;
        border-width: 1px;
        border-color: #030303;
    }

    .intro {
        padding: 20px;
    }

    .intro1 {
        margin-top: 22px;
        padding: 30px 0px;
        background-color: #f3f3f3;
    }

    .intro1 li {
        list-style: none;
    }

    .intro1 a {
        color: #0E97EE !important;
    }

    .intro a {
        color: #030303;
        font-weight: 400;
        font-size: 16px;
    }

    .lead {}

    .left-align img {
        margin-top: 20px;
    }

    .drop-caps p:first-child::first-letter {
        color: #030303;
        display: block;
        float: left;
        font-size: 75px;
        line-height: 60px;
        text-transform: uppercase;
        margin-right: 10px;
        margin-top: 5px;
        padding: 4px;
    }

    .drop-caps.full p:first-child::first-letter {
        background: #030303 none repeat scroll 0 0 !important;
        color: #ffffff;
        margin-right: 15px;
        padding: 20px;
        font-size: 36px;
        font-family: Georgia;
        border-radius: 5px;
    }

    mark {
        background-color: #0E97EE;
        color: #ffffff;
        padding: 0 10px;
    }

    a:focus,
    a:hover {
        color: #0E97EE;
    }

    .intro2 i {
        padding-right: 10px;
        font-size: 21px;
    }

    .intro2 {
        background-color: #0E97EE;
        border: 2px solid #2187BB;
        border-radius: 5px;
        color: #ffffff;
        font-style: italic;
        padding: 10px 20px;
    }

    .intro2 p {
        padding: 0;
    }

    .intro2 a {
        color: #ffffff;
        font-weight: bold;
    }

    .intro1 ul {
        padding: 0 30px;
    }

    .btn-primary {
        background-color: #0E97EE;
        border: 1px solid #2187BB;
    }

    .btn-primary:hover {
        background-color: #2187BB;
        border: 1px solid #0E97EE;
    }

    .btn-info {
        background-color: #545454;
        border: 1px solid #656565;
    }

    .btn-info:hover {
        background-color: #656565;
        border: 1px solid #545454;
    }

    .navbar-inverse .navbar-nav>.active>a,
    .navbar-inverse .navbar-nav>.active>a:hover,
    .navbar-inverse .navbar-nav>.active>a:focus {
        background-color: #0E97EE;
        color: #fff;
    }

    .navbar-inverse {
        background-color: #030303;
        border: 0;
    }

    .navbar-inverse li.btn {
        border-radius: 0 !important;
        padding: 0;
    }

    .navbar-inverse li.btn a {
        color: #ffffff !important;
    }

    /*Docs*/



    .sub-title {
        font-size: 30px;
        margin-top: 50px;
        margin-bottom: 10px;
        padding-bottom: 0;
    }

    .sub-title a {
        font-size: 16px;
    }

    .section.docs-heading {
        padding: 10px 0;
    }

    .affix {
        position: static;
    }

    @media (min-width: 992px) {

        .affix,
        .affix-bottom {
            width: 213px
        }

        .affix {
            position: fixed;
            top: 20px
        }

        .affix-bottom {
            position: absolute
        }

        .affix .bs-docs-sidenav,
        .affix-bottom .bs-docs-sidenav {
            margin-top: 0;
            margin-bottom: 0
        }
    }

    @media (min-width: 1200px) {

        .affix,
        .affix-bottom {
            width: 263px
        }
    }

    /* SIDEBAR */

    @media (min-width: 768px) {
        .docs-sidebar {
            padding-top: 20px;
        }
    }

    /* all links */
    .docs-sidebar .nav>li>a {
        color: #111;
        border-left: 2px solid transparent;
        font-size: 15px;
        font-weight: 400;
    }

    /* nested links */
    .docs-sidebar .nav .nav>li>a {
        padding-left: 40px;
        font-size: 14px;
    }

    /* hover links */
    .docs-sidebar .nav>li:not(.active)>a:hover {
        color: #0E97EE;
        text-decoration: none;
        background-color: transparent;
    }

    /* focus links */
    .docs-sidebar .nav>li>a:focus {
        text-decoration: none;
        background-color: transparent;
    }

    /* active links */
    .docs-sidebar .nav>.active>a {
        color: #0E97EE;
        text-decoration: none;
        background-color: transparent;
        border-left-color: #0E97EE;
    }

    /* all active links */
    .docs-sidebar .nav>.active>a,
    .docs-sidebar .nav>.active:hover>a,
    .docs-sidebar .nav>.active:focus>a {
        font-weight: 700;
    }

    /* nested active links */
    .docs-sidebar .nav .nav>.active>a,
    .docs-sidebar .nav .nav>.active:hover>a,
    .docs-sidebar .nav .nav>.active:focus>a {
        font-weight: 500;
    }

    @media (min-width: 992px) {
        .docs-sidebar .nav ul {
            display: none;
            padding-bottom: 10px;
        }

        .docs-sidebar .nav>.active>ul {
            display: block
        }
    }

    /*Syntax Highlighter : Sublime Theme */

    .syntaxhighlighter {
        background-color: #2b303b !important;
        padding: 15px 0;
        margin: 2em 0 1em 0 !important;
    }

    .syntaxhighlighter a,
    .syntaxhighlighter div,
    .syntaxhighlighter code,
    .syntaxhighlighter table,
    .syntaxhighlighter table td,
    .syntaxhighlighter table tr,
    .syntaxhighlighter table tbody,
    .syntaxhighlighter table thead,
    .syntaxhighlighter table caption,
    .syntaxhighlighter textarea {
        line-height: 1.3em !important;
    }

    .syntaxhighlighter .line.alt1 {
        background-color: #2b303b !important;
    }

    .syntaxhighlighter .line.alt2 {
        background-color: #2b303b !important;
    }

    .syntaxhighlighter .string,
    .syntaxhighlighter .string a {
        color: #90be8c !important;
    }

    .syntaxhighlighter .color1,
    .syntaxhighlighter .color1 a {
        color: #d08770 !important;
    }

    .syntaxhighlighter .plain,
    .syntaxhighlighter .plain a {
        color: #c0c5ce !important;
    }

    .syntaxhighlighter .keyword {
        color: #bf616a !important;
    }

    .syntaxhighlighter .gutter {
        color: #757a84 !important;
    }

    .syntaxhighlighter .line.highlighted.alt1,
    .syntaxhighlighter .line.highlighted.alt2 {
        background-color: #333E49 !important;
    }

    .syntaxhighlighter .gutter .line.highlighted {
        background-color: #343d46 !important;
        color: #757a84 !important;
    }

    .syntaxhighlighter .value {
        color: #96b5b4 !important;
    }

    /*css*/
    /* .syntaxhighlighter .css.plain,
.syntaxhighlighter .css.plain a {
    color: #d08770 !important;
} 

.syntaxhighlighter .css.keyword {
    color: #c0c5ce !important;
}*/

    .syntaxhighlighter .color3,
    .syntaxhighlighter .color3 a {
        color: #b48ead !important;
    }

    /*js*/

    .syntaxhighlighter .js.keyword {
        color: #b48ead !important;
    }



