.ErrorTooltip {
    cursor: default;
    margin-top: 6px;
    position: absolute;
    display: block;
    padding: 7px;
    color: #564511;
    background-color: #f4c22b;
    font-size: 0.9em;
    font-weight: bold;
    z-index: 2;
    border-radius: 8px;
    // border-top-left-radius: 0;
    box-shadow: 1px 3px 5px #b9c2d4;
    transition: all;

    &::before {
        content: "\63";
        font-family: "pct";
        position: absolute;
        left: 8px;
        top: -2px;
        z-index: 1001;
        font-size: 30px;
        line-height: 0;

        color: #f4c22b;
        text-shadow: 0 -2px 2px rgba(65, 76, 78, 0.12);
    }
}

